.footer-wrapper {
  display: flex;
  width: 100%;
  flex-direction: row;
  align-items: center; }
  .footer-wrapper button {
    outline: none;
    position: absolute;
    right: 20%;
    transform: rotate(180deg);
    background: #333;
    border-radius: 50%;
    cursor: pointer;
    width: 64px;
    height: 64px;
    border: none; }
    .footer-wrapper button > svg {
      width: 32px;
      height: 32px; }

.footer {
  width: max-content;
  height: fit-content;
  padding: 5px 15px;
  z-index: -1;
  display: flex;
  flex-direction: column;
  border-radius: 2px 3px 2px 3px;
  background: linear-gradient(0deg, #e0ffcc, #ffd6cc);
  color: #666;
  justify-content: flex-end;
  align-items: center; }
  .footer p {
    white-space: nowrap; }
