.flex-v-center,
.flex-v {
  display: flex;
  flex-direction: column;
  width: 100%; }

.flex-v-center {
  align-items: center; }

.flex-h-center {
  justify-content: center; }

.flex-h-center,
.flex-h {
  display: flex;
  flex-flow: row, wrap;
  width: 100%; }

.v-splitter {
  width: 0;
  height: 0;
  border: solid 1px; }

.oa-underline {
  position: relative;
  width: fit-content;
  white-space: nowrap;
  z-index: 1001; }
  .oa-underline:hover::after {
    opacity: 0.6; }
  .oa-underline::after {
    content: '';
    position: absolute;
    right: 0;
    bottom: 5px;
    width: 100%;
    height: 100%;
    border: #8187d5 solid;
    opacity: 0.3;
    border-width: 0 0 8px 0; }

.oa-hover-underline {
  position: relative;
  color: #4a4ca3;
  width: fit-content; }
  @media (max-width: 768px) {
    .oa-hover-underline {
      border: solid #8187d5;
      border-width: 0 0 3px 0; } }
  @media (min-width: 768px) {
    .oa-hover-underline {
      white-space: nowrap; }
      .oa-hover-underline:hover::after {
        opacity: 0.3; }
      .oa-hover-underline::after {
        content: '';
        position: absolute;
        right: 0;
        bottom: 5px;
        z-index: 1001;
        width: 100%;
        height: 100%;
        border: #8187d5 solid;
        opacity: 0;
        border-width: 0 0 8px 0; } }

.reading-settings {
  max-width: 700px; }
  .reading-settings p {
    font-size: 1rem;
    line-height: 1.5; }
    @media (max-width: 768px) {
      .reading-settings p {
        font-size: 0.95rem; } }

.hidden {
  display: none; }

.dark-bg {
  background: #2c2c2c; }

.fit-content {
  width: fit-content; }

.oa-link {
  text-decoration-line: underline !important;
  transition-duration: 0.3s;
  color: #8187d5; }
  .oa-link:hover {
    color: #c7cbf0; }

.psuedo {
  padding: 0px 5px;
  border-radius: 4px;
  background: #fffae0; }

.oa-notice {
  padding: 3px 7px;
  border: solid 10px #967cff;
  border-width: 0 0 0 10px;
  background: #b9ffd0;
  border-radius: 2px 3px 2px 3px; }

body {
  color: #333;
  overflow-x: hidden;
  font-family: 'Noto Sans SC', sans-serif;
  -webkit-font-smoothing: antialiased;
  margin: 0;
  min-height: 100vh; }

img {
  object-fit: cover; }

a {
  text-decoration: none; }

.page-wrapper {
  min-height: 100vh; }

.sidebar-active {
  color: #4a4ca3 !important; }

@media (min-width: 768px) {
  .oa-sidebar {
    transition-duration: 0.3s;
    transition-timing-function: ease-in-out;
    transition-delay: 0.1s; }
    .oa-sidebar + .sidebar-placeholder ~ * {
      transition-delay: 0.1s;
      transition-duration: 0.3s;
      transition-timing-function: ease-in-out; }
    .oa-sidebar:not(:hover) {
      background: transparent; }
    .oa-sidebar:hover {
      width: 160px;
      transition-delay: 0s; }
      .oa-sidebar:hover + .sidebar-placeholder ~ * {
        transition-delay: 0s;
        transform: translateX(120px); } }

@media (max-width: 768px) {
  .sidebar-placeholder {
    margin: 0; }
  .oa-sidebar {
    height: min-content;
    bottom: 50px;
    padding: 10px 1px !important; }
  .oa-sidebar:hover > .sidebar-item-detail:not(:last-child) {
    padding: 0 2px 10px 0; } }

@media (min-width: 768px) {
  .html-post {
    margin: 0 8vw 0 calc(8vw - 40px); } }

.post-page {
  display: grid;
  justify-content: center; }

.html-post {
  margin: 0 5vw;
  display: flex;
  flex-flow: row wrap; }

.post-page a,
.html-post a {
  text-decoration-line: underline !important;
  transition-duration: 0.3s;
  color: #8187d5; }
  .post-page a:hover,
  .html-post a:hover {
    color: #c7cbf0; }

.post-page img,
.html-post img {
  max-width: 55vw;
  margin: 1.5vw;
  align-self: center;
  transition-duration: 0.15s;
  transition-timing-function: ease-in-out; }
  .post-page img:active,
  .html-post img:active {
    transform: scale(1.7); }

.oa-md {
  max-width: 700px;
  margin: 0 5vw 10vw;
  font-size: 1rem;
  line-height: 1.5;
  word-wrap: break-word; }
  .oa-md p {
    display: flex;
    flex-direction: column;
    font-size: 1rem; }

h3,
h2 {
  margin-bottom: 0; }

iframe {
  border-radius: 2px 3px 2px 3px;
  width: 100%;
  height: 400px; }

@media (max-width: 768px) {
  iframe {
    height: auto;
    width: auto; }
  .html-post h1,
  .post-page h1 {
    margin-left: 5vw; }
  .html-post img,
  .post-page img {
    max-width: 100vw; }
  .oa-md {
    width: 90vw; }
    .oa-md p {
      font-size: 0.95rem; } }

.yellow {
  color: #ecaa11; }

.green {
  color: #0ac55e; }
