.flex-v-center,
.flex-v {
  display: flex;
  flex-direction: column;
  width: 100%; }

.flex-v-center {
  align-items: center; }

.flex-h-center {
  justify-content: center; }

.flex-h-center,
.flex-h {
  display: flex;
  flex-flow: row, wrap;
  width: 100%; }

.v-splitter {
  width: 0;
  height: 0;
  border: solid 1px; }

.oa-underline {
  position: relative;
  width: fit-content;
  white-space: nowrap;
  z-index: 1001; }
  .oa-underline:hover::after {
    opacity: 0.6; }
  .oa-underline::after {
    content: '';
    position: absolute;
    right: 0;
    bottom: 5px;
    width: 100%;
    height: 100%;
    border: #8187d5 solid;
    opacity: 0.3;
    border-width: 0 0 8px 0; }

.oa-hover-underline {
  position: relative;
  color: #4a4ca3;
  width: fit-content; }
  @media (max-width: 768px) {
    .oa-hover-underline {
      border: solid #8187d5;
      border-width: 0 0 3px 0; } }
  @media (min-width: 768px) {
    .oa-hover-underline {
      white-space: nowrap; }
      .oa-hover-underline:hover::after {
        opacity: 0.3; }
      .oa-hover-underline::after {
        content: '';
        position: absolute;
        right: 0;
        bottom: 5px;
        z-index: 1001;
        width: 100%;
        height: 100%;
        border: #8187d5 solid;
        opacity: 0;
        border-width: 0 0 8px 0; } }

.reading-settings {
  max-width: 700px; }
  .reading-settings p {
    font-size: 1rem;
    line-height: 1.5; }
    @media (max-width: 768px) {
      .reading-settings p {
        font-size: 0.95rem; } }

.hidden {
  display: none; }

.dark-bg {
  background: #2c2c2c; }

.fit-content {
  width: fit-content; }

.oa-link {
  text-decoration-line: underline !important;
  transition-duration: 0.3s;
  color: #8187d5; }
  .oa-link:hover {
    color: #c7cbf0; }

.psuedo {
  padding: 0px 5px;
  border-radius: 4px;
  background: #fffae0; }

.oa-notice {
  padding: 3px 7px;
  border: solid 10px #967cff;
  border-width: 0 0 0 10px;
  background: #b9ffd0;
  border-radius: 2px 3px 2px 3px; }

body {
  color: #333;
  overflow-x: hidden;
  font-family: 'Noto Sans SC', sans-serif;
  -webkit-font-smoothing: antialiased;
  margin: 0;
  min-height: 100vh; }

img {
  object-fit: cover; }

a {
  text-decoration: none; }

.page-wrapper {
  min-height: 100vh; }

.sidebar-active {
  color: #4a4ca3 !important; }

@media (min-width: 768px) {
  .oa-sidebar {
    transition-duration: 0.3s;
    transition-timing-function: ease-in-out;
    transition-delay: 0.1s; }
    .oa-sidebar + .sidebar-placeholder ~ * {
      transition-delay: 0.1s;
      transition-duration: 0.3s;
      transition-timing-function: ease-in-out; }
    .oa-sidebar:not(:hover) {
      background: transparent; }
    .oa-sidebar:hover {
      width: 160px;
      transition-delay: 0s; }
      .oa-sidebar:hover + .sidebar-placeholder ~ * {
        transition-delay: 0s;
        transform: translateX(120px); } }

@media (max-width: 768px) {
  .sidebar-placeholder {
    margin: 0; }
  .oa-sidebar {
    height: min-content;
    bottom: 50px;
    padding: 10px 1px !important; }
  .oa-sidebar:hover > .sidebar-item-detail:not(:last-child) {
    padding: 0 2px 10px 0; } }

.post {
  padding-left: 15px;
  padding-right: 15px;
  margin-top: 0;
  margin-bottom: 20px;
  font-weight: normal;
  counter-increment: posts;
  max-width: 30vw;
  display: flex;
  justify-content: space-between;
  word-wrap: break-word;
  display: block;
  -webkit-column-break-inside: avoid; }
  .post:hover {
    transform: none; }
  @media (max-width: 768px) {
    .post {
      max-width: initial;
      padding-left: 10px;
      padding-right: 10px; }
      .post img {
        max-height: 110px;
        margin-left: 10px;
        height: min-content; } }
  .post .card-text {
    flex: 2;
    display: grid;
    align-content: flex-start; }
  .post .tag-list {
    align-self: flex-end;
    display: flex;
    flex-flow: row wrap;
    margin-top: 7px;
    font-size: 0.7rem; }
    .post .tag-list .post-tag {
      white-space: nowrap;
      border-radius: 2px 3px 2px 3px;
      background: whitesmoke;
      color: #8187d5;
      height: fit-content;
      margin-right: 5px;
      padding: 0 3px; }
    .post .tag-list .tag-wip {
      background-color: #ffe9d5; }
  .post .post-title {
    font-weight: bold;
    font-size: 18px;
    margin-right: 5px;
    cursor: pointer;
    color: #333; }
  .post .post-content {
    color: #666;
    font-size: 1rem; }
    @media (max-width: 768px) {
      .post .post-content {
        font-size: 0.95rem; } }
  .post .post-img {
    flex: 1;
    object-fit: cover;
    border-radius: 2px 3px 2px 3px;
    margin: 5px 0 5px 15px;
    transition-duration: 0.3s;
    transition-timing-function: ease-in-out; }
    .post .post-img:hover {
      z-index: 1111;
      transform: scale(2.3);
      cursor: default; }
    @media (max-width: 768px) {
      .post .post-img {
        transform: scale(1); }
        .post .post-img:hover {
          z-index: 9999;
          transform: scale(2.5);
          transform-origin: right; } }

.post-lr {
  display: flex; }
  .post-lr .post-img {
    width: 0px;
    flex-basis: auto;
    max-height: 130px; }

.fa-link {
  margin: 0 0 3px 3px;
  font-size: 0.7rem; }
